import {
    AfterViewInit,
    Component,
    EventEmitter,
    forwardRef,
    HostListener,
    Input,
    OnChanges,
    OnInit,
    Output,
    Renderer2,
    SimpleChanges,
    ViewChild,
    OnDestroy
} from '@angular/core';
import { FormGroup, NG_VALUE_ACCESSOR, NgControl } from '@angular/forms';
import { MatAutocomplete, MatAutocompleteTrigger } from '@angular/material';

import { Observable, of, Subscription } from 'rxjs';
import { map, startWith } from 'rxjs/internal/operators';

@Component({
    selector: 'th2-mat-async-typeahead',
    templateUrl: './th2-mat-async-typeahead.component.html',
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            // tslint:disable-next-line: no-forward-ref
            useExisting: forwardRef(() => Th2MatAsyncTypeaheadComponent),
            multi: true
        }
    ]
})
export class Th2MatAsyncTypeaheadComponent implements AfterViewInit, OnChanges, OnInit, OnDestroy {
    // component params
    @Input()
    public form: FormGroup;
    @Input()
    public formControlFieldName: string;
    @Input()
    public value?: string;
    @Input()
    public placeholder?: string;
    @Input()
    public options: string[];
    @Input()
    public classList?: string | string[] = 'th2-light-theme';
    @Input()
    public autocompleteDisabled?: boolean;

    // mat-input params
    @Input()
    public disabled?: boolean;
    @Input()
    public listGroupOptions;
    @Input()
    public required?: boolean;
    @Input()
    public readonly?: boolean;

    // mat-hint params
    @Input()
    public leftHintLabel?: string;
    @Input()
    public rightHintLabel?: string;

    // mat-error
    @Input()
    public errorMessages?: object[] = [];

    public formControl: any;
    public internalDisabled = false;

    public subscription: Subscription = null;

    @Output()
    public change: EventEmitter<string> = new EventEmitter();

    @Output()
    public error: EventEmitter<any> = new EventEmitter();
    public asyncOptions: Observable<string[]>;

    // TODO: ngModel
    @ViewChild(NgControl)
    private fieldControl: any;
    @ViewChild(MatAutocomplete)
    private matAutocomplete: MatAutocomplete;
    @ViewChild(MatAutocompleteTrigger)
    private matAutocompleteTrigger: any;
    @ViewChild('select')
    private selector: any;

    private actualClass: string[] = [];
    private initialized = false;

    constructor(private renderer: Renderer2) {}

    public ngOnInit() {
        if (this.form) {
            this.formControl = this.form.get(this.formControlFieldName);
        }
        this.asyncOptions = of([]);
    }

    public ngAfterViewInit() {
        this.initPanelCdk();
        this.internalDisabled = this.disabled;
        this.initialized = true;

        this.subscription = this.formControl.valueChanges.subscribe((data) => {
            this.change.emit(data);
        });
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (this.initialized) {
            if (this.options) {
                this.asyncOptions = of(this.options);
            } else {
                this.asyncOptions = of([]);
            }
            if (changes['disabled']) {
                this.internalDisabled = this.disabled;
            }
            if (changes['classList']) {
                if (typeof this.classList === 'string') {
                    this.changeClass([this.classList]);
                } else {
                    this.changeClass(this.classList);
                }
            }
        }
    }

    public ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }

    get selectValue() {
        return this.value;
    }

    set selectValue(value) {
        this.value = value;
        this.propagateChange(this.value);
        this.change.emit();
    }

    @HostListener('input')
    @HostListener('change')
    // Implementation for writeValue of ControlValueAccessor Interface
    public writeValue(value: string) {
        if (value !== undefined) {
            this.value = value;
            this.change.emit();
        }
    }

    // Implementation for registerOnChange of ControlValueAccessor Interface
    public registerOnChange(fn: (value: string) => void) {
        this.propagateChange = fn;
    }

    // Implementation for registerOnTouched of ControlValueAccessor Interface
    public registerOnTouched(fn: (value: string) => void) {
        //
    }

    public changeClass(newClass: string[]) {
        if (this.matAutocompleteTrigger) {
            const element = this.matAutocompleteTrigger._overlayRef.overlayElement;
            this.actualClass.forEach((actualClass) => {
                this.renderer.removeClass(element, actualClass);
            });
            this.addArrayClass(newClass);
        }
    }

    public showErrorMessages(): string {
        let errorString = '';
        if (this.errorMessages && this.errorMessages.length > 0) {
            if (
                this.fieldControl &&
                this.fieldControl.control &&
                this.fieldControl.control.errors
            ) {
                this.errorMessages.forEach((error: any) => {
                    if (
                        error.field &&
                        error.message &&
                        this.fieldControl.control.errors[error.field]
                    ) {
                        errorString += error.message;
                    }
                });
            }
        } else {
            errorString = null;
        }

        return errorString;
    }

    // Propogates changed value of Textbox
    private propagateChange = (value: string) => {
        //
    };

    private initPanelCdk() {
        if (this.classList && this.matAutocompleteTrigger) {
            this.matAutocompleteTrigger.openPanel();
            this.matAutocompleteTrigger.closePanel();

            if (typeof this.classList === 'string') {
                this.changeClass([this.classList]);
            } else {
                this.changeClass(this.classList);
            }
        }
    }

    private addStringClass(newClass: string) {
        this.renderer.addClass(this.matAutocompleteTrigger._overlayRef.overlayElement, newClass);
        this.actualClass = [newClass];
    }

    private addArrayClass(newClass: string[]) {
        const newClasses = [];
        newClass.forEach((innerClass) => {
            this.renderer.addClass(
                this.matAutocompleteTrigger._overlayRef.overlayElement,
                innerClass
            );
            newClasses.push(innerClass);
        });
        this.actualClass = newClasses;
    }
}
