import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';

import { Thin2LoggerModule } from 'thin2-log';
import { Thin2ComponentsModule } from 'thin2-components';
import { Thin2Config } from 'thin2-config';

import { ComponentsModule } from '../components/components.module';

import { Th2GenericFormComponent } from './th2-generic-form.component';
import { Th2GenericFormControlComponent } from './th2-generic-form-control.component';
import { Th2GenericFormGroupComponent } from './th2-generic-form-group.component';
import { MatAutocompleteModule, MatFormFieldModule, MatInputModule } from '@angular/material';

// tslint:disable-next-line: max-line-length
import { Th2MatAsyncTypeaheadComponent } from './th2-mat-async-typeahead/th2-mat-async-typeahead.component';
import { ErrorStateMatcher, ShowOnDirtyErrorStateMatcher } from '@angular/material';

@NgModule({
    declarations: [
        Th2GenericFormComponent,
        Th2GenericFormControlComponent,
        Th2GenericFormGroupComponent,
        Th2MatAsyncTypeaheadComponent
    ],
    exports: [
        Th2GenericFormComponent,
        Th2GenericFormGroupComponent,
        Th2GenericFormControlComponent
    ],
    imports: [
        CommonModule,
        ComponentsModule,
        ReactiveFormsModule,
        Thin2ComponentsModule,
        Thin2Config,
        Thin2LoggerModule,
        MatAutocompleteModule,
        MatFormFieldModule,
        MatInputModule
    ],
    providers: [
        {
            provide: ErrorStateMatcher,
            useClass: ShowOnDirtyErrorStateMatcher
        }
    ]
})
export class Th2GenericFormModule {}
