import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';

import { Thin2Logger } from 'thin2-log';
import { OutputQuoteStyle } from 'uglify-js';
import { Th2MatSelectComponent } from '../../../../thin2/thin2-components';
import { LogoIconViewComponent } from '../../views/demo/logo-icon-view/logo-icon-view.component';

import { Th2GenericFormAttributesModel } from './models/th2-generic-form-attributes.model';
import { Th2GenericFormInputTypes } from './th2-generic-form-input-types.enum';

@Component({
    selector: 'th2-generic-form-control-component',
    templateUrl: './th2-generic-form-control.component.html'
})
export class Th2GenericFormControlComponent implements OnDestroy, OnInit {
    @Input()
    public controlConfig: Th2GenericFormAttributesModel;
    @Input()
    public formGroup: FormGroup;

    public chips: string[] = [];
    public formTypes = Th2GenericFormInputTypes;
    public subscriptions: Subscription[] = [];

    constructor(private LOGGER: Thin2Logger, private formBuilder: FormBuilder) {}

    public ngOnInit() {
        this.LOGGER.debug(`Rendering control ${this.controlConfig.name}`);

        const formControl = this.formGroup.get(String(this.controlConfig.name));

        if (!formControl) {
            const controlConfig = {
                value: this.controlConfig.default,
                disabled: this.controlConfig.disabled
            };

            const validators = this.controlConfig.displayed ? this.controlConfig.validators : [];
            const control = this.formBuilder.control(controlConfig, validators);
            this.formGroup.addControl(this.controlConfig.name, control);
        }

        if (this.controlConfig.type === Th2GenericFormInputTypes.CHIP) {
            // tslint:disable-next-line: max-line-length
            const valueChangesSubscr = this.formGroup
                .get(this.controlConfig.name)
                .valueChanges.subscribe((value) => {
                    this.chips = value;
                });
            this.subscriptions.push(valueChangesSubscr);

            if (this.controlConfig.default) {
                if (Array.isArray(this.controlConfig.default)) {
                    this.controlConfig.default.forEach((item) => {
                        this.chips.push(item);
                    });
                    this.formGroup.get(this.controlConfig.name).patchValue(this.chips);
                }
            }

            if (!this.controlConfig['chipInnerControlName']) {
                this.controlConfig['chipInnerControlName'] = 'chip';
            }
            this.formGroup.addControl(
                this.controlConfig['chipInnerControlName'],
                new FormControl('')
            );
        }
    }

    /**
     * <p><code>OnDestroy</code> lifecycle handler.
     * Responsible for unsubscriptions</p>
     */
    public ngOnDestroy(): void {
        this.LOGGER.debug('ProductLandingComponent - OnDestroy');
        if (this.subscriptions && this.subscriptions.length > 0) {
            this.subscriptions.forEach((subscription) => {
                if (subscription) {
                    subscription.unsubscribe();
                }
            });
        }
    }

    // 控件是否必须
    public isFieldRequired(): boolean {
        if (this.controlConfig && this.controlConfig.validators) {
            return this.controlConfig.validators.some((v) => {
                return Validators.required === v;
            });
        }

        return false;
    }

    public onClickAddChip(): void {
        this.chips = this.chips ? this.chips : [];

        if (this.formGroup.get(this.controlConfig['chipInnerControlName']).value) {
            this.chips.push(this.formGroup.get(this.controlConfig['chipInnerControlName']).value);
            this.formGroup.get(this.controlConfig.name).patchValue(this.chips);
            this.formGroup.get(this.controlConfig.name).patchValue(this.chips);
        }
        this.formGroup.get(this.controlConfig['chipInnerControlName']).patchValue('');
    }

    public onClickRemoveChip(chipIndex: number): void {
        this.chips = this.chips ? this.chips : [];

        this.chips.splice(chipIndex, 1);
        this.formGroup.get(this.controlConfig.name).patchValue(this.chips);
    }
}
